import React, { useState } from 'react';
import { AppBar, Toolbar, Typography, IconButton, MenuItem, Menu, Backdrop, CircularProgress, withStyles, ListItemText, ListItemIcon, ListItem, List, Divider, Drawer, useTheme, useMediaQuery, Tabs, Tab, Button, Avatar } from '@material-ui/core';
import { AccountCircle, Add, Assignment, ChevronLeft, ChevronRight, LockOpen, MenuSharp, Person, Update, } from '@material-ui/icons';
import { Tooltip, Box } from '@material-ui/core';
import { Link, useNavigate } from "react-router-dom";
import { COLOR_BACKGROUND_VERDER_AGUA, DrawerHeader, TEXT_HEADER, useStylesAppBarra } from '../styles/styles';
import { useDispatch } from 'react-redux';
import { setToken } from '../redux/actions/action';
import logo from '../assets/logo.png';

const drawerWidth = 240;

function HeaderCustom() {
    const [anchorEl, setAnchorEl] = useState(null);
    const [cargando, setCargando] = useState('');
    const [open, setOpen] = useState(false);
    const [value, setValue] = useState(0);



    const theme = useTheme();
    const isMatch = useMediaQuery(theme.breakpoints.down('sm'));
    console.log("isMatch", isMatch);
    const classes = useStylesAppBarra();
    const isMenuOpen = Boolean(anchorEl);

    let history = useNavigate();
    const dispatch = useDispatch();

    //---------------------------------acceder a los datos del LocalStorage
    let dataView = window.localStorage.getItem('dataUser');
    const { username } = JSON.parse(dataView || '{}');

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };


    //-------------------------------rutas
    const agregarAtm = () => {
        history('/agregar');
    }

    const updateMedicos = () => {
        history('/formulario');
    }


    //--------------------------------cerrar sesion de login
    const closeLogin = () => {
        setCargando(true);
        setTimeout(() => {
            localStorage.removeItem('dataUser');
            dispatch(setToken(null));
        }, 2000);
    }

    //--------------------------------abrir menu de usuario
    const handleProfileMenuOpen = (event) => {
        setAnchorEl(event.currentTarget);
    };

    //--------------------------------cerrar menu de usuario
    const handleMenuClose = () => {
        setAnchorEl(null);
    };

    //-----------------------------abrir el menu de navegacion
    const handleDrawerOpen = () => {
        setOpen(true);
    };

    //-----------------------------cerrar el menu
    const handleDrawerClose = () => {
        setOpen(false);
    };

    const renderMenu = (
        <Menu
            anchorEl={anchorEl}
            anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
            }}
            //id={menuId}
            keepMounted
            transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
            }}
            open={isMenuOpen}
            onClose={handleMenuClose}
        >
            <MenuItem className={classes.contentItem} onClick={handleMenuClose}>{username}</MenuItem>
            <MenuItem className={classes.contentItem} onClick={closeLogin}>Cerrar Sesion</MenuItem>
        </Menu>
    );


    const arrayMenu = [
        {
            id: 1,
            name: 'Inicio',
            icon: <Add style={{ color: COLOR_BACKGROUND_VERDER_AGUA }} />,
            action: agregarAtm
        },
        {
            id: 2,
            name: 'Hombres',
            icon: <Person style={{ color: COLOR_BACKGROUND_VERDER_AGUA }} />,
            action: updateMedicos
        },
        {
            id: 3,
            name: 'Mujeres',
            icon: <Person style={{ color: COLOR_BACKGROUND_VERDER_AGUA }} />,
            action: closeLogin
        }
    ]


    return (
        <Box sx={{ flexGrow: 1 }}>
                <Toolbar style={{}}>
                    {isMatch ? (
                        <>
                            <div style={{
                                display: 'flex',
                                justifyContent: 'space-between',
                                alignItems: 'center',
                                width: '100%'
                            }}>
                                <div style={{ display: 'flex', alignItems: 'center' }}>
                                    <IconButton
                                        color="inherit"
                                        aria-label="open drawer"
                                        onClick={handleDrawerOpen}
                                        edge="start"
                                        sx={{ mr: 2, ...(open && { display: 'none' }) }}
                                    >
                                        <MenuSharp style={{ color: COLOR_BACKGROUND_VERDER_AGUA }} />
                                    </IconButton>

                                    <Link to='/home' className={classes.link}>
                                        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }} color='textPrimary'>
                                            Gym
                                        </Typography>
                                    </Link>
                                </div>


                                <div style={{}}>
                                    <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
                                        <IconButton
                                            size="large"
                                            edge="end"
                                            aria-label="account of current user"
                                            //aria-controls={menuId}
                                            aria-haspopup="true"
                                            onClick={handleProfileMenuOpen}
                                            color="inherit"
                                        >
                                            <AccountCircle style={{ color: COLOR_BACKGROUND_VERDER_AGUA }} />
                                        </IconButton>
                                    </Box>
                                </div>
                            </div>


                            <Drawer
                                sx={{
                                    width: drawerWidth,
                                    flexShrink: 0,
                                    '& .MuiDrawer-paper': {
                                        width: drawerWidth,
                                        boxSizing: 'border-box',
                                    },
                                }}
                                variant="persistent"
                                anchor="left"
                                open={open}
                                //cambiar el color del drawer
                                classes={{
                                    paper: classes.colorDrawer
                                }}

                            >
                                <DrawerHeader>
                                    <IconButton onClick={handleDrawerClose} style={{ color: COLOR_BACKGROUND_VERDER_AGUA }} >
                                        {theme.direction === 'ltr' ? <ChevronLeft /> : <ChevronRight />}
                                    </IconButton>
                                </DrawerHeader>
                                <Divider />

                                {arrayMenu.map((item, index) => (
                                    <List key={item.id}>
                                        <ListItem button onClick={item.action} >
                                            <ListItemIcon>
                                                {item.icon}
                                            </ListItemIcon>
                                            <ListItemText primary={item.name} />

                                        </ListItem>
                                        {index === arrayMenu.length - 1 ? null : <Divider />}
                                    </List>
                                ))}
                            </Drawer>
                        </>
                    ) : (

                        <div style={{
                            display: 'flex',
                            justifyContent: 'space-between',
                            alignItems: 'center',
                            width: '100%'
                        }}>
                            {/* <Avatar alt="Remy Sharp" src={logo} sizes={300} /> */}
                            <img src={logo}  alt="logo" style={{width: 120, height: 70}}/>

                            <Tabs
                                sx={{ marginLeft: "auto" }}
                                // indicatorColor="primary"
                                // textColor="primary"
                                onChange={handleChange}
                                value={value}
                                TabIndicatorProps={{
                                    style: {
                                        backgroundColor: TEXT_HEADER,
                                    }
                                }}
                                //cambiar color del texto
                                style={{ color: TEXT_HEADER }}
                            >
                                <Tab label="Inicio" />
                                <Tab label="Hombres" />
                                <Tab label="Mujeres" />
                            </Tabs>


                            <Tooltip
                                title="Perfil"
                            >
                                <IconButton
                                    size="large"
                                    edge="end"
                                    aria-label="account of current user"
                                    //aria-controls={menuId}
                                    aria-haspopup="true"
                                    onClick={handleProfileMenuOpen}
                                    color="inherit"
                                >
                                    <AccountCircle style={{ color: TEXT_HEADER }} />
                                </IconButton>
                            </Tooltip>
                        </div>
                    )}
                </Toolbar>

            {renderMenu}

            <Backdrop className={classes.backdrop} open={cargando}>
                <CircularProgress color="inherit" />
            </Backdrop>
        </Box>
    );
}

export default HeaderCustom;